@import './css/team.css';
@import './css/swiperCustom.css';
@import './css/globalComponent.css';
@import './css/product.css';
@import './css/homepage.css';
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(1px); }
    to   { opacity: 1; transform: translateY(0); }
  }
  
  /* Active l’animation pour tout élément dont la classe contient "section_" */
  @media (prefers-reduced-motion: no-preference) {
    @supports (animation-timeline: view()) {
      [class*="section_"]:not([class*="hero"]) {
        animation-name: fadeIn;
        animation-duration: 200ms;
        animation-timing-function: ease-out;
        animation-fill-mode: both;
  
        /* Scroll-driven */
        animation-timeline: view();
        /* start and end of the animation */
        animation-range: 5% 30%;
      }
    }
  }